@import url(https://fonts.googleapis.com/css?family=Montserrat);
body {
  font-family: "Montserrat", cursive;
}
.main-container {
  margin: 20px;
}

.main-container .logoContainer {
  padding: 40px 0;
}
.main-container .logoContainer img {
  max-height: 58px;
}
h1 {
  width: 100%;
}

h2 {
  margin-top: 0;
  font-size: 20px;
}

.list-container {
  display: flex;
}
.result-container,
.saved-container {
  margin-right: 20px;
  border-radius: 10px;
  border: solid 2px #f9eded;
  padding: 20px;
  background: #f3f8fb;
  width: 33%;
  max-width: 250px;
}

.result-container h2 {
  padding-top: 0;
}

.card {
  max-width: 250px;
  display: flex;
  flex-direction: column;
  border: 2px solid #dce5e6;
  border-radius: 10px;
  margin-bottom: 20px;
  position: relative;
  background-color: #fff;
}

.card .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background-color: red; */
  display: none;
}
.card:hover .overlay {
  display: block;
}

.card .overlay .buttonContainer {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: table;
}

.card .overlay .buttonContainer .buttonMiddle {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}
.card .price {
  padding: 5px 10px;
  min-height: 20px;
  font-size: 14px;
  color: #3f51b5;
  font-weight: bold;
}

button {
  padding: 10px 30px;
  border: none;
  color: white;
  border-radius: 20px;
  font-weight: bold;
}

button.btnRemove {
  background: red;
}

button.btnAdd {
  background: green;
}

.card .logo {
  text-align: left;
  max-width: 100%;
  padding: 5px;
}

.card .logo img {
  max-width: 100%;
}

.main-image {
  max-width: 100%;
}
.main-image img {
  width: 100%;
  /* max-height: 60px; */
}

